import React, { Component, Fragment } from 'react';
import Display from '../../components/Display';
import { Card, Divider, Row, Col } from 'antd';
import StandardTable from './StandardTable';
import StandardTableMD from './StandardTable.md';
import ExpandTable from './ExpandTable';
import ExpandTableMD from './ExpandTable.md';
import SelectTable from './SelectTable';
import SelectTableMD from './SelectTable.md';
import TreeTable from './TreeTable';
import TreeTableMD from './TreeTable.md';
import API from './API';

const MockStandardTable = () => (
  <Fragment>
    <Card>
      <h1>StandardTable</h1>
      标准表格
    </Card>
    <Divider />
    <Row gutter={24}>
      <Col span={24}>
        <Display
          markdown={StandardTableMD}
          title="基本用法"
          desc="介绍基本用法"
        >
          <StandardTable />
        </Display>
      </Col>
    </Row>
    <Divider />
    <Row gutter={24}>
      <Col span={24}>
        <Display
          markdown={ExpandTableMD}
          title="展开"
          desc="介绍表格展开的用法，使用时需要对每个 row 对象加上初始值 { expand : false }，详见代码示例"
        >
          <ExpandTable />
        </Display>
      </Col>
    </Row>
    <Divider />
    <Row gutter={24}>
      <Col span={24}>
        <Display
          markdown={SelectTableMD}
          title="选择"
          desc="介绍表格选择的用法"
        >
          <SelectTable />
        </Display>
      </Col>
    </Row>
    <Divider />
    <Row gutter={24}>
      <Col span={24}>
        <Display markdown={TreeTableMD} title="树形" desc="介绍树形表格的用法">
          <TreeTable />
        </Display>
      </Col>
    </Row>
    <Divider />
    <API />
  </Fragment>
);

export default MockStandardTable;



